.App {
    min-height: 100vh;
    text-align: center;
    display: grid;
    align-items: center;
    justify-content: center;
}

.input {

    border: 3px solid rgba(92, 92, 92, 0.74);
    border-radius: 5px;
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: fit-content;

}

.input p {

    margin: 0;

}

.input button {

    padding: 10px;
    border: none;
    outline: none;
    background: indigo;
    color: white;
    font-weight: 700;
    margin-top: 10px;

}

.input button:focus {

    box-shadow:0 0 0 2px black;

}
