.minesweeper {

    border: 3px solid rgba(92, 92, 92, 0.74);
    border-radius: 5px;
    padding: 10px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: fit-content;

}

.popup{

    position: absolute;
    top: 50%;
    left: 50%;
    width: 300px;
    height: fit-content;
    padding: 10px;
    border: 2px solid black;
    border-radius: 5px;
    z-index: 1;
    transform: translate(-50%, -50%);
    background: white;

}

.popup p{

    margin: 0;
    margin-bottom: 10px;
    
}

.popup__buttons {

    display: flex;
    justify-content: space-around;

}

.popup__buttons button {

    padding: 10px;
    width: 200px;
    border: none;
    outline: none;
    background: indigo;
    color: white;
    font-weight: 700;
    margin-bottom: 10px;

}

.rows {

    display: flex;
    height: 25px;

}

.element, .bomb, .zero_element {

    height: 25px;
    width: 25px;
    background: white;
    color: white;
    font-weight: 700;
    display: grid;
    align-items: center;
    transform: scale(1);
    transition: background-color 0.3s;
    border: 1px solid black;
    font-size: 0;

}

.bomb.open {

    background: rgb(255, 63, 63);
    font-size: 0.7rem;

}

.element.open {
    background: rgb(255, 180, 255);
    font-size: 0.7rem;
    color: white;
}

.zero_element.open {

    background: rgb(155, 155, 155);
    color: white;

}